import Loader from "react-loader-spinner";
import React from "react";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

export default function Spinner() {
  return (
    <Loader
      className="loadar"
      type="Hearts"
      color="#00BFFF"
      height={100}
      width={100}
      timeout={1000} //3 secs
    />
  );
}
